
import React from "react";
import Card from "./Card";
import Data from "./Data";
import './index.css';


const App = () => {
  return (
    <>
      <h1 className="heading_style"> List of top 9 Netflix Series in 2020 </h1>

      {Data.map((netdata) => {
        return (
          <Card
            imgscr={netdata.imgscr}
            sname={netdata.sname}
            title={netdata.title}
            links={netdata.links}
          />
        );
      })}
    </>
  );
};

export default App;